@import "https://fonts.googleapis.com/css2?family=Bitter:wght@600&display=swap";
body {
  background-color: var(--third-color);
  color: var(--first-background-color);
  letter-spacing: .02em;
  font-family: Roboto, sans-serif;
  font-size: 16px;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.header {
  width: 100%;
  margin-top: 30px;
  position: fixed;
}

.header-container {
  max-width: 985px;
  margin: 0 auto;
}

.nav-list {
  justify-content: center;
  gap: 30px;
  display: flex;
}

.nav-link {
  padding: 11px 18px;
  font-family: Bitter, serif;
  font-size: 16px;
  position: relative;
}

.nav-link:after {
  content: "";
  height: 2px;
  width: 100%;
  background-color: #caff34;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(0);
}

.nav-link:hover:after {
  transform: scaleX(1);
}

